.slider {
  position: relative;
  background: rgba(129, 216, 208, 0.46);
  margin-bottom: 110px;
}

.slide {
  display: flex;
  align-items: center;
  justify-content: center;
}

.slide__nav {
  padding: 40px;
  cursor: pointer;
  background: inherit;
  border: none;
  transition: 0.3s;

  &:hover {
    background: rgb(129, 216, 208);
  }
}

.slide__nav--forward {
  transform: scale(-1, 1);
}

.slider__controls {
  position: absolute;
  top: 326px;
  right: 83px;
  z-index: 20;
  font-size: 0;
}

.slider__controls label {
  display: inline-block;
  width: 14px;
  height: 14px;
  margin-left: 18px;
  vertical-align: top;
  background-color: transparent;
  border: 1px solid #000000;
  border-radius: 50%;
  cursor: pointer;
  position: relative;
}

.slider__controls label::after {
  content: "";
  width: 6px;
  height: 6px;
  border: 1px solid #000000;
  border-radius: 50%;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#slide-1:checked ~ .slider #slide1,
#slide-2:checked ~ .slider #slide2,
#slide-3:checked ~ .slider #slide3,
#slide-4:checked ~ .slider #slide4,
#slide-5:checked ~ .slider #slide5 {
  display: flex;
}
#slide-1:checked ~ .slider [for="slide-1"]::after,
#slide-2:checked ~ .slider [for="slide-2"]::after,
#slide-3:checked ~ .slider [for="slide-3"]::after,
#slide-4:checked ~ .slider [for="slide-4"]::after,
#slide-5:checked ~ .slider [for="slide-5"]::after {
  opacity: 1;
}
