.nav {
  margin-right: 100px;
  margin-bottom: 40px;
  margin-left: 100px;
}

.nav__list {
  list-style-type: none;
  padding-left: 0;
  display: flex;
  justify-content: space-between;
}

.nav__link {
  text-transform: uppercase;
  color: @colorText;
  font-size: 18px;
  padding: 3px 5px;
  border-bottom: 1px solid transparent;
  transition: 0.6s;

  &:hover {
    color: #a59552;
    border-bottom: 1px solid @colorGreen;
  }
}

.nav__link--active {
  color: #a59552;
  border-bottom: 1px solid @colorGreen;
}
